@font-face {
  font-family: "Gilroy";
  src: url("../fonts/../fonts/Gilroy-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/../fonts/Gilroy-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/../fonts/Gilroy-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/../fonts/Gilroy-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/../fonts/Gilroy-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}

/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #383B45;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  width: 125px;
  height: 125px;
  border: solid 10px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-left-color: rgba(255, 255, 255, 0.7);
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

/* global styles*/

body {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  font-size: 19px;
  line-height: 1.6;
  color: #9599A5;
  overflow-x: hidden;
}

img {
  filter: sepia(100%);
  transition: all 0.5s;
  transition-delay: 0.7s;
}

.uninverted {
  filter: sepia(0);
  transition: all 0.5s;
  transition-delay: 0.7s;
}

:root {
  scroll-behavior: smooth;
}

.none {
  display: none;
}

.visible {
  display: block;
}

.no-scroll {
  overflow: hidden;
}

.hidden {
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
  user-select: none;
  pointer-events: none;
}

.untranslate {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 0.5s;
}

.wrapper {
  margin: 0 auto;
  max-width: 1500px;
}

.container {
  margin: 0 auto;
  max-width: 1500px;
  padding: 0 15px;
}

.title-left {
  margin-bottom: 35px;
  font-weight: 700;
  font-size: 42px;
  line-height: 1.2;
  text-align: left;
  color: #010001;
}

.decor-left {
  width: 100%;
  max-width: 250px;
  height: 2px;
  margin-bottom: 35px;
  transform-origin: right;
  transform: scale(0);
  background: linear-gradient(90deg, #F14339 0.4%, rgba(241, 67, 57, 0) 100.8%);
  filter: drop-shadow(0px 6px 24px rgba(255, 35, 0, 0.3));
  border-radius: 2px;
}

.title-center {
  margin-bottom: 35px;
  font-weight: 700;
  font-size: 42px;
  line-height: 1.2;
  text-align: center;
  color: #010001;
}

.decor-center {
  width: 100%;
  max-width: 250px;
  height: 2px;
  margin: 0 auto 35px auto;
  transform-origin: center;
  transform: scale(0);
  background: linear-gradient(90deg, rgba(241, 67, 57, 0) 0.4%, rgb(241, 67, 57) 50%, rgba(241, 67, 57, 0) 100.8%);
  filter: drop-shadow(0px 6px 24px rgba(255, 35, 0, 0.3));
  border-radius: 2px;
}

.learn-more__left-decor {
  width: 100%;
  max-width: 200px;
  height: 2px;
  margin-bottom: 15px;
  transform-origin: right;
  transform: scale(0);
  background: linear-gradient(90deg, #F14339 0.4%, rgba(241, 67, 57, 0) 100.8%);
  filter: drop-shadow(0px 6px 24px rgba(255, 35, 0, 0.3));
  border-radius: 2px;
}

.unscaled {
  transform: scale(1);
  transition: all 1s;
  transition-delay: 0.5s;
}

.title-item {
  font-weight: 700;
  font-size: 18px;
  line-height: 1.2;
  text-align: left;
  color: #010001;
}

.button-cart {
  display: inline-block;
  padding: 20px 67px;
  background: #242734;
  border: 1.4px solid #F14339;
  box-shadow: 0px 6px 24px rgba(255, 35, 0, 0.3);
  border-radius: 12px;
  font-weight: 800;
  font-size: 16px;
  line-height: 34px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
  transition: all 0.3s;
}

.button-cart:hover {
  box-shadow: 0px 6px 24px rgba(255, 35, 0, 0.6);
}

.icon {
  transition: all 0.5s;
  transition-delay: 1s;
}

/* Header styles */

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 76px;
  padding: 26px 62px;
}

.header__logo {
  margin-right: 81px;
  opacity: 0;
  transform: translate(-500px, 0);
  transition: all 0.5s;
  transition-delay: 0.3s;
}

.logo__svg {
  width: 125px;
  height: 24px;
  fill: #fff;
}

.nav {
  width: 100%;
  max-width: 500px;
  margin-left: -29px;
  margin-top: -13px;
  margin-right: 50px;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.nav__item {
  opacity: 0;
  transform: translateY(-200px);
  transition: all 0.5s;
}

.nav__item.untranslate:nth-child(1) {
  transition-delay: 0.5s;
}

.nav__item.untranslate:nth-child(2) {
  transition-delay: 0.6s;
}

.nav__item.untranslate:nth-child(3) {
  transition-delay: 0.7s;
}

.nav__item.untranslate:nth-child(4) {
  transition-delay: 0.8s;
}

.nav__item.untranslate:nth-child(5) {
  transition-delay: 1s;
}

.nav__link {
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.5px;
  color: #ACAFBF;
  -webkit-text-stroke: 0.1px #ACAFBF;
}

.nav__link::before {
  content: attr(data-text);
  position: absolute;
  top: 1px;
  left: 0;
  width: 0%;
  height: 100%;
  color: #fff;
  -webkit-text-stroke: 0 #ACAFBF;
  overflow: hidden;
  animation: hover-text-rev 0.5s ease-in forwards;
}

.nav__link:hover::before {
  animation: hover-text 0.5s ease-in forwards;
}

@keyframes hover-text {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes hover-text-rev {
  0% {
    width: 100%;
  }

  100% {
    width: 0%;
  }
}

.nav__svg-box {
  display: none;
}

.header-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95px;
  opacity: 0;
  transform: translate(200px, 0);
  transition: all 0.5s;
  transition-delay: 0.4s;
}

.header-right.untranslate {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 0.8s;
}

.language {
  position: relative;
  width: 37px;
  margin-top: -5px;
  margin-left: -2px;
  margin-right: 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  color: #ACAFBF;
}

.language__header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  transition: all 0.3s;
  cursor: pointer;
}

.language__header:hover {
  color: #fff;
  transition: all 0.3s;
}

.language__header:hover .language__svg {
  stroke: #fff;
  transition: all 0.3s;
}

.language__svg {
  width: 16px;
  height: 16px;
  fill: none;
  stroke: #ACAFBF;
}

.language__list {
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  z-index: 1;
}

.language__item {
  list-style: none;
  transition: all 0.3s;
  cursor: pointer;
}

.language__item:hover {
  color: #fff;
  transition: all 0.3s;
}

.language__item:not(:last-child) {
  margin-bottom: 3px;
}

.visible {
  display: block;
}

.cart {
  position: relative;
  width: 18px;
  height: 24px;
  transform: translate(-6px, -5px);
}

.cart__counter {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: -8px;
  width: 15px;
  height: 15px;
  background: #F14339;
  border: 1.4px solid #373B44;
  border-radius: 50%;
  font-weight: 700;
  font-size: 10px;
  line-height: 1.2;
  color: #fff;
}

.cart__svg {
  width: 18px;
  height: 24px;
  fill: #fff;
}

.burger {
  display: none;
  position: relative;
  width: 26px;
  height: 18px;
  z-index: 10;
  margin-top: -5px;
  transform: translate(200px, 0);
  opacity: 0;
  transition: all 0.3s;
}

.burger:hover .burger__span::before {
  width: 100%;
  transition: all 0.3s;
}

.burger.untranslate {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 0.5s;
  transition-delay: 0.7s;
}

.burger__span {
  position: absolute;
  left: 0;
  width: 26px;
  height: 3px;
  border-radius: 1px;
  background-color: #ACAFBF;
  transition: all 0.3s;
}

.burger__span::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #fff;
  transition: all 0.3s;
}

.burger__span:hover::before {
  width: 100%;
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
  transition: all 0.3s;
}

.burger__span-two {
  top: 50%;
  transition: all 0.3s;
}

.burger__span-three {
  top: 100%;
  transition: all 0.3s;
}

.span-one-transform {
  top: 50%;
  transform: rotate(45deg);
  transition: all 0.3s;
}

.span-two-transform {
  transform: rotate(-45deg);
  transition: all 0.3s;
}

.span-three-transform {
  display: none;
}

.line {
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  top: 76px;
  background: linear-gradient(90deg, rgba(242, 244, 255, 0) 0%, rgba(242, 244, 255, 0.2) 8.56%, rgba(242, 244, 255, 0.4) 50%, rgba(242, 244, 255, 0.2) 91.53%, rgba(242, 244, 255, 0) 100%);
}

.hero {
  position: relative;
  width: 100%;
  padding: 75px 66px 54px 190px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.hero__content {
  width: 100%;
  max-width: 541px;
  padding: 205px 0 0 0;
  flex-shrink: 0.5;
}

.hero__subtitle {
  font-size: 12px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 3px;
  color: #FBC5B4;
  text-transform: uppercase;
  margin-bottom: 15px;
  opacity: 0;
  transform: translate(-200px, 0);
  transition: all 0.5s;
}

.hero__subtitle:nth-child(1) {
  transition-delay: 0.9s;
}

.hero__title {
  margin-bottom: 27px;
}

.hero__title .hero__title-span:nth-child(13) {
  transition-delay: 1s;
}

.hero__title .hero__title-span:nth-child(12) {
  transition-delay: 1.1s;
}

.hero__title .hero__title-span:nth-child(11) {
  transition-delay: 1.2s;
}

.hero__title .hero__title-span:nth-child(10) {
  transition-delay: 1.3s;
}

.hero__title .hero__title-span:nth-child(9) {
  transition-delay: 1.4s;
}

.hero__title .hero__title-span:nth-child(8) {
  transition-delay: 1.5s;
}

.hero__title .hero__title-span:nth-child(7) {
  transition-delay: 1.6s;
}

.hero__title .hero__title-span:nth-child(6) {
  transition-delay: 1.7s;
}

.hero__title .hero__title-span:nth-child(5) {
  transition-delay: 1.8s;
}

.hero__title .hero__title-span:nth-child(4) {
  transition-delay: 1.9s;
}

.hero__title .hero__title-span:nth-child(3) {
  transition-delay: 2s;
}

.hero__title .hero__title-span:nth-child(2) {
  transition-delay: 2.1s;
}

.hero__title .hero__title-span:nth-child(1) {
  transition-delay: 2.2s;
}

.hero__title-span {
  display: inline-block;
  font-size: 72px;
  font-weight: 800;
  line-height: 1.1;
  color: #fff;
  opacity: 0;
  transform: translate(-200px, 0);
}

.hero__text {
  font-size: 19px;
  font-weight: 400;
  line-height: 1.57;
  letter-spacing: 0.5px;
  max-width: 541px;
  margin-bottom: 58px;
  opacity: 0;
  transform: translate(200px, 0);
}

.hero__text.untranslate {
  transition-delay: 0.9s;
}

.untranslate {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 0.5s;
}

.hero__down {
  position: absolute;
  left: 62px;
  bottom: 50px;
  display: block;
  width: 62px;
  height: 62px;
  border-radius: 50%;
  overflow: hidden;
  transition: all 0.3s;
}

.svg-pagination-ellipse {
  position: absolute;
  left: 0;
  top: 0;
  width: 62px;
  height: 62px;
  animation: round 12s linear infinite;
}

@keyframes round {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.svg-pagination-arrow {
  position: absolute;
  left: 25px;
  top: 20px;
  animation: arrow 3s linear infinite;
  width: 14px;
  height: 21px;
}

@keyframes arrow {
  0% {
    transform: scale(0.8);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(0.8);
    transition: all 0.5s;
  }
}

.hero__slider {
  width: 100%;
  max-width: 600px;
  padding: 135px 0 0 0;
}

.slider {
  position: relative;
  width: 100%;
  max-width: 600px;
  aspect-ratio: 1/1;
  overflow: hidden;
}

.slider__cards {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  height: 100%;
  display: inline-flex;
  transform: translateX(0);
  transition: all 0.5s;
}

.slider__card {
  min-width: 100%;
  height: 100%;
  transition: all 0.5s;
}

.slider-img {
  width: 100%;
  height: 100%;
  padding: 20px;
  object-fit: cover;
  -webkit-user-drag: none;
  filter: drop-shadow(10px 0 10px rgba(255, 255, 255, 0));
  transform-origin: center;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.7s, filter 1s, opacity 1s;
}

.scaled {
  transform: scale(1);
  opacity: 1;
  filter: drop-shadow(0 10px 10px rgba(255, 255, 255, 0.3));
  transition: transform 0.7s, filter 1.5s, opacity 1s;
}

.hero__slider-control {
  position: relative;
  width: 100%;
  height: 32px;
  padding-right: 132px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero__slider-button {
  width: 32px;
  height: 32px;
  border: 1px solid transparent;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.hero__slider-button:not(:last-child) {
  margin-right: 3px;
}

.hero__slider-button--active {
  border: 1.4px solid #F14339;
  transition: all 0.3s;
}

.hero__button-dot {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #888888;
}

.hero__button-dot--active {
  background-color: #fff;
  width: 6px;
  height: 6px;
  transition: all 0.3s;
}

.cart-wrapper {
  background: linear-gradient(180deg, #383B45 0%, #090D1D 100%);
}

.cart-wrapper .header {
  position: relative;
}

.cart-wrapper .header .nav {
  max-width: 300px;
}

.cart-wrapper .title-center {
  color: #fff;
}

.cart-wrapper .footer__nav {
  margin-left: 50px;
}

.add {
  width: 100%;
  max-width: 1300px;
  padding: 30px 15px;
  margin: 0 auto;
}

.add .decor-center {
  margin-bottom: 60px;
}

.add__inner {
  position: relative;
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
}

.add__goods {
  width: 100%;
  max-width: 875px;
  display: grid;
  grid-template-columns: repeat(3, 275px);
  grid-template-rows: auto;
  justify-content: center;
  gap: 20px;
}

.card {
  width: 275px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(1, 0, 1, 0.7);
  transition: all 0.3s;
  background: linear-gradient(180deg, #383B45 0%, #090D1D 151.01%);
  color: #fff;
}

.card:hover {
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
  transition: all 0.3s;
}

.card__img {
  width: 200px;
  height: 175px;
  object-fit: cover;
  display: block;
  margin: 0 auto;
  filter: drop-shadow(0 0 5px rgba(1, 0, 1, 0.7));
}

.card__title {
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 30px;
  line-height: 1.2;
  text-align: center;
  letter-spacing: 3px;
  color: #fff;
}

.card__characteristics {
  width: 100%;
  transition: all 0.5s;
}

.card__characteristics-item {
  width: 85%;
  padding-left: 20px;
  font-size: 17px;
}

.card__characteristics-item:last-child {
  margin-bottom: 20px;
}

.card__characteristics-mean {
  margin-left: 5px;
  font-size: 16px;
  color: rgba(241, 67, 57, 0.7);
}

.card__counter {
  width: 100%;
  height: 35px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(1, 0, 1, 0.7);
}

.card__control {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
}

.card__control:hover {
  background-color: #F14339;
  transition: all 0.3s;
}

.card__current {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card__button {
  width: 100%;
  padding: 5px;
  border-radius: 10px;
  background: #242734;
  border: 1.4px solid #F14339;
  border-radius: 10px;
  font-size: 17px;
  line-height: 1.6;
}

.card__button:hover {
  background-color: #F14339;
  transition: all 0.3s;
}

.add-goods-button {
  display: none;
  padding: 5px 10px;
  background: #242734;
  border: 1.4px solid #F14339;
  border-radius: 10px;
  font-size: 17px;
  line-height: 1.6;
}

.add-goods-button:hover {
  background-color: #F14339;
  transition: all 0.3s;
}

.add__cart-box {
  position: sticky;
  top: 0;
  max-width: 295px;
}

.card__alert {
  margin-bottom: 10px;
  font-style: 17px;
  color: rgba(241, 67, 57, 0.7);
}

