  transition: all 0.3s;
}

.demonstration__pin::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 18.7%;
  height: 18.7%;
  border-radius: 50%;
