.testimonials__slider {
  width: 100%;
  max-width: 1100px;
  min-height: 388px;
  margin: 0 auto 10px auto;
  overflow: hidden;
}

.testimonials__slider-track {
  position: relative;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 388px;
  display: inline-flex;
}

.testimonials__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 465px;
  min-height: 388px;
  padding-top: 45px;
  padding-bottom: 30px;
  padding-right: 5px;
  transition: all 0.5s;
}

.testimonials__card + .testimonials__card {
  width: 635px;
  padding-left: 170px;
}

.testimonials__card-img {
  display: block;
  border-radius: 50%;
  margin: 0 auto 20px auto;
}

.testimonials__card-text {
  flex: 1;
  margin-bottom: 47px;
  font-weight: 500;
  font-size: 19px;
  line-height: 1.58;
  text-align: center;
  color: #C8AAA1;
}

.testimonials__card-name {
  display: block;
  margin-bottom: 11px;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  color: #010001;
}

.testimonials__card-profession {
  display: block;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  color: #C8AAA1;
}

.testimonials__slider-control {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 100%;
  height: 48px;
}

.testimonials__button {
  position: absolute;
  top: 0;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #fff;
  transition: all 0.3s;
}

.testimonials__button:hover .tesnimonials__svg {
  stroke: #f00a0a;
  transition: all 0.3s;
}

.testimonials__button-left {
  left: 5.2%;
}

.testimonials__button-right {
  right: 5.2%;
}

.tesnimonials__svg {
  width: 8px;
  height: 14px;
  stroke: #888888;
}

.pre-order {
  display: grid;
  grid-template-columns: repeat(2, minmax(474px, 1fr));
  width: 100%;
  background: linear-gradient(180deg, #383B45 0%, #090D1D 151.01%);
}

.pre-order__content {
  display: grid;
  align-content: center;
  justify-content: center;
}

.pre-order__content-inner {
  width: 100%;
  max-width: 360px;
}

.pre-order__title {
  color: #fff;
}

.pre-order-content-text {
  margin-bottom: 25px;
  font-weight: 400;
  font-size: 19px;
  line-height: 1.58;
  color: #9599A5;
}

.pre-order__content-price {
  margin-bottom: 45px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;
  color: #9599A5;
}

.pre-order__content-sum {
  margin-left: 16px;
  font-weight: 800;
  font-size: 17px;
  line-height: 1.5;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;
}

.header-img {
  width: 100%;
  object-fit: cover;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 15px;
  z-index: 50;
  display: grid;
  place-content: center;
  background: rgba(1, 0, 1, 0.5);
}

.none {
  display: none;
}

.cart-message {
  width: 100%;
  max-width: 500px;
  padding: 60px 20px;
  display: grid;
  place-content: center;
  gap: 40px;
  border-radius: 10px;
  background: linear-gradient(180deg, #383B45 0%, #090D1D 100%);
  color: #fff;
}

.cart-message__text {
  letter-spacing: 2px;
}

.cart-message__button {
  justify-self: end;
  max-width: 150px;
  padding: 10px 20px;
  background: #242734;
  border: 1.4px solid #F14339;
  box-shadow: 0px 6px 24px rgba(255, 35, 0, 0.3);
  border-radius: 7px;
  text-transform: uppercase;
}

.cart-message__button:hover {
  background-color: #F14339;
  transition: all 0.3s;
}

.footer {
  width: 100%;
  padding: 30px 125px 35px 125px;
  background: linear-gradient(180deg, #383B45 0%, #090D1D 100%);
}

.footer__inner {
  width: 100%;
  max-width: 1250px;
}

.footer__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 8px 0 39px 0;
  border-bottom: 1px solid rgba(136, 136, 136, 0.2);
}

.footer__top .logo {
  order: 1;
}

.footer__nav {
  width: 100%;
  max-width: 440px;
  margin-right: 50px;
  margin-left: -30px;
  order: 2;
}

.footer__nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.footer__nav-link {
  position: relative;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  color: #A5A5A5;
  -webkit-text-stroke: 0.1px #ACAFBF;
}

.footer__nav-link::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  color: #fff;
  -webkit-text-stroke: 0 #ACAFBF;
  overflow: hidden;
  animation: hover-text-rev 0.3s ease-in forwards;
}

.footer__nav-link:hover::before {
  animation: hover-text 0.3s ease-in forwards;
}

@keyframes hover-text {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes hover-text-rev {
  0% {
    width: 100%;
  }

  100% {
    width: 0%;
  }
}

.social {
  order: 3;
  width: 100%;
  max-width: 136px;
  height: 24px;
  display: grid;
  grid-template-columns: repeat(3, 24px);
  column-gap: 32px;
  justify-content: space-between;
}

.social__link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 12px;
  transition: all 0.3s;
}

.social__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 3px;
  width: 19px;
  height: 0;
  opacity: 0;
  border-radius: 50%;
  z-index: 1;
  background-color: #fff;
  transition: all 0.3s;
}

.social__link:hover::before {
  height: 23px;
  opacity: 1;
  transition: all 0.3s;
}

.social__svg {
  position: relative;
  z-index: 2;
  width: 24px;
  height: 24px;
  fill: #888888;
}

.footer__bottom {
  width: 100%;
  padding-top: 30px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;
  color: #888888;
}

.footer__bottom-list {
  list-style: none;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__bottom-item:nth-child(1) {
  order: 1;
}

.footer__bottom-item:nth-child(2) {
  order: 2;
}

.footer__bottom-item:nth-child(3) {
  order: 3;
}

.footer__bottom-item + .footer__bottom-item {
  margin-left: 56px;
}

.footer__link {
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;
  color: #888888;
  -webkit-text-stroke: 0.1px #ACAFBF;
}

.footer__link::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  color: #fff;
  -webkit-text-stroke: 0 #ACAFBF;
  overflow: hidden;
  animation: hover-link-rev 0.3s ease-in forwards;
}

.footer__link:hover::before {
  animation: hover-link 0.3s ease-in forwards;
}

@keyframes hover-link {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes hover-link-rev {
  0% {
    width: 100%;
  }

  100% {
    width: 0%;
  }
}

