@media (max-width: 390px) {
  .demonstration__pin-active {
    transform: scale(1.5);
  }

  .footer .logo {
    margin-right: 70px;
  }

  .footer__top {
    padding: 8px 0 39px 0;
  }

  .footer__nav__list {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 50%;
  }

  .footer__bottom-list {
    flex-wrap: wrap;
    align-items: space-between;
    justify-content: flex-start;
  }

  .footer__bottom-item:nth-child(1) {
    order: 3;
    margin-top: 15px;
  }

  .footer__bottom-item:nth-child(2) {
    order: 1;
    margin-right: 30px;
  }

  .footer__bottom-item:nth-child(3) {
    order: 2;
    margin-right: 30px;
  }
}

@media (max-width: 360px) {
  .title-left,
  .title-center {
    font-size: 32px;
  }

  .button-cart {
    font-size: 15px;
  }

  .hero__title {
    max-width: 230px;
  }

  .hero__title-span {
    font-size: 38px;
  }

  .hero__text {
    font-size: 17px;
    margin-bottom: 38px;
  }

  .design-process__text {
    font-size: 17px;
  }

  .possibilities__text {
    font-size: 17px;
    margin-bottom: 35px;
    max-width: 290px;
  }
}